"use client";
import React, { useState, useEffect } from "react";
import Link from "next/link";
import { 
  ChevronLeft, 
  Search, 
  X,
  Filter,
  Star,
  TrendingUp,
  Code,
  Layers,
  Database,
  Palette,
  Briefcase,
  Clock,
  Users,
  ArrowRight
} from "lucide-react";
import { 
  roadmapsData, 
  popularTags, 
  searchRoadmaps 
} from "./roadmapsData";

// Category definitions with better organization
const categories = {
  programming: {
    title: "Programming Languages",
    icon: <Code className="w-5 h-5" />,
    color: "blue",
    description: "Master core programming languages"
  },
  frameworks: {
    title: "Frameworks & Libraries",
    icon: <Layers className="w-5 h-5" />,
    color: "indigo",
    description: "Modern development frameworks"
  },
  databases: {
    title: "Databases & Backend",
    icon: <Database className="w-5 h-5" />,
    color: "green",
    description: "Data management and server technologies"
  },
  design: {
    title: "Design & Creative",
    icon: <Palette className="w-5 h-5" />,
    color: "purple",
    description: "UI/UX and creative skills"
  },
  specialization: {
    title: "Specialized Fields",
    icon: <Briefcase className="w-5 h-5" />,
    color: "orange",
    description: "Specialized technology domains"
  }
};

// Categorize roadmaps based on their content
const categorizeRoadmap = (roadmap) => {
  const title = roadmap.title.toLowerCase();
  const description = roadmap.description.toLowerCase();

  // Programming Languages
  if (['javascript', 'python', 'java', 'typescript', 'c++', 'c language', 'php', 'rust', 'go', 'swift', 'kotlin'].some(lang => title.includes(lang))) {
    return 'programming';
  }

  // Frameworks & Libraries
  if (['react', 'next.js', 'django', 'nest.js', 'flutter', 'springboot', 'express js'].some(fw => title.includes(fw))) {
    return 'frameworks';
  }

  // Databases & Backend
  if (['sql', 'database', 'backend', 'node js', 'server', 'devops', 'cloud'].some(db => title.includes(db) || description.includes(db))) {
    return 'databases';
  }

  // Design & Creative
  if (['ui/ux', 'graphic', 'video', 'design'].some(design => title.includes(design) || description.includes(design))) {
    return 'design';
  }

  // Specialization (AI, Security, etc.)
  return 'specialization';
};

// Add categories to roadmaps data
const categorizedRoadmaps = roadmapsData.map(roadmap => ({
  ...roadmap,
  category: categorizeRoadmap(roadmap)
}));

// Difficulty and duration mapping (you can customize these)
const getDifficultyInfo = (title) => {
  const beginnerTechs = ['javascript', 'python', 'html', 'css', 'excel'];
  const advancedTechs = ['ai', 'system design', 'blockchain', 'cyber security', 'c++'];

  if (beginnerTechs.some(tech => title.toLowerCase().includes(tech))) {
    return { difficulty: 'Beginner', duration: '2-3 months' };
  }
  if (advancedTechs.some(tech => title.toLowerCase().includes(tech))) {
    return { difficulty: 'Advanced', duration: '6-8 months' };
  }
  return { difficulty: 'Intermediate', duration: '3-5 months' };
};

export default function TechRoadmap() {
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [filteredRoadmaps, setFilteredRoadmaps] = useState(categorizedRoadmaps);
  const [hoveredCard, setHoveredCard] = useState(null);

  // Filter roadmaps based on search and category
  useEffect(() => {
    let filtered = categorizedRoadmaps;

    // Apply search filter
    if (searchQuery.trim()) {
      filtered = searchRoadmaps(searchQuery).map(roadmap => ({
        ...roadmap,
        category: categorizeRoadmap(roadmap)
      }));
    }

    // Apply category filter
    if (selectedCategory !== "all") {
      filtered = filtered.filter(roadmap => roadmap.category === selectedCategory);
    }

    setFilteredRoadmaps(filtered);
  }, [searchQuery, selectedCategory]);

  // Handle search tag click
  const handleTagClick = (tag) => {
    setSearchQuery(tag);
  };

  // Get roadmaps by category
  const getRoadmapsByCategory = (categoryKey) => {
    return filteredRoadmaps.filter(roadmap => roadmap.category === categoryKey);
  };

  // Get featured roadmaps (first few from each category)
  const getFeaturedRoadmaps = () => {
    const featured = [];
    Object.keys(categories).forEach(categoryKey => {
      const categoryRoadmaps = getRoadmapsByCategory(categoryKey);
      featured.push(...categoryRoadmaps.slice(0, 2));
    });
    return featured;
  };

  const featuredRoadmaps = searchQuery || selectedCategory !== "all" ? filteredRoadmaps : getFeaturedRoadmaps();

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 pt-6 pb-12">
        {/* Header */}
        <div className="mb-8">
          <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-8">
            {/* Navigation */}
            <div className="flex items-center justify-between mb-6">
              <Link href="/" className="flex items-center text-gray-600 hover:text-blue-600 transition-colors">
                <ChevronLeft className="w-5 h-5 mr-1" />
                <span className="text-sm font-medium">Back to Home</span>
              </Link>
              <div className="flex items-center space-x-2 text-sm text-gray-500">
                <Users className="w-4 h-4" />
                <span>{categorizedRoadmaps.length} Learning Paths</span>
              </div>
            </div>

            <div className="text-center">
              <h1 className="text-4xl font-bold text-gray-900 mb-4">
                Tech Learning Roadmaps
              </h1>
              <p className="text-xl text-gray-600 max-w-2xl mx-auto">
                Discover structured learning paths for programming languages, frameworks, and specialized tech skills
              </p>
            </div>
          </div>
        </div>

        {/* Search and Filters */}
        <div className="mb-8">
          <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-6">
            {/* Search Bar */}
            <div className="relative mb-6">
              <input
                type="text"
                placeholder="Search roadmaps..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full px-4 py-4 pl-12 pr-12 rounded-xl border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent bg-gray-50"
              />
              <Search className="w-5 h-5 text-gray-400 absolute left-4 top-4.5" />
              {searchQuery && (
                <button
                  onClick={() => setSearchQuery("")}
                  className="absolute right-4 top-4.5 text-gray-400 hover:text-gray-600"
                >
                  <X className="w-5 h-5" />
                </button>
              )}
            </div>

            {/* Category Filters */}
            <div className="flex flex-wrap gap-3 mb-4">
              <button
                onClick={() => setSelectedCategory("all")}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                  selectedCategory === "all"
                    ? "bg-blue-600 text-white shadow-md"
                    : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                }`}
              >
                All Categories
              </button>
              {Object.entries(categories).map(([key, category]) => (
                <button
                  key={key}
                  onClick={() => setSelectedCategory(key)}
                  className={`px-4 py-2 rounded-lg text-sm font-medium transition-all flex items-center space-x-2 ${
                    selectedCategory === key
                      ? "bg-blue-600 text-white shadow-md"
                      : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                  }`}
                >
                  {category.icon}
                  <span>{category.title}</span>
                </button>
              ))}
            </div>

            {/* Popular Tags */}
            <div className="flex flex-wrap gap-2">
              <span className="text-sm text-gray-500 flex items-center">
                <Star className="w-4 h-4 mr-1" />
                Popular:
              </span>
              {popularTags.map((tag) => (
                <button
                  key={tag}
                  onClick={() => handleTagClick(tag)}
                  className="px-3 py-1 text-xs rounded-full bg-blue-50 hover:bg-blue-100 text-blue-600 transition-colors border border-blue-200"
                >
                  {tag}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Results */}
        {searchQuery || selectedCategory !== "all" ? (
          // Search/Filter Results
          <div className="mb-8">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-gray-900">
                {searchQuery ? `Search Results` : categories[selectedCategory]?.title}
                <span className="text-lg text-gray-500 ml-2">({filteredRoadmaps.length})</span>
              </h2>
            </div>

            {filteredRoadmaps.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {filteredRoadmaps.map((roadmap) => (
                  <RoadmapCard
                    key={roadmap.id}
                    roadmap={roadmap}
                    hoveredCard={hoveredCard}
                    setHoveredCard={setHoveredCard}
                  />
                ))}
              </div>
            ) : (
              <NoResultsMessage onClear={() => { setSearchQuery(""); setSelectedCategory("all"); }} />
            )}
          </div>
        ) : (
          // Category-wise Display
          <>
            {/* Featured Section */}
            <div className="mb-12">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-gray-900 flex items-center">
                  <Star className="w-6 h-6 mr-2 text-yellow-500" />
                  Featured Roadmaps
                </h2>
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {featuredRoadmaps.slice(0, 6).map((roadmap) => (
                  <RoadmapCard
                    key={roadmap.id}
                    roadmap={roadmap}
                    hoveredCard={hoveredCard}
                    setHoveredCard={setHoveredCard}
                    featured={true}
                  />
                ))}
              </div>
            </div>

            {/* Categories Sections */}
            {Object.entries(categories).map(([categoryKey, categoryInfo]) => {
              const categoryRoadmaps = getRoadmapsByCategory(categoryKey);
              if (categoryRoadmaps.length === 0) return null;

              return (
                <div key={categoryKey} className="mb-12">
                  <div className="flex items-center justify-between mb-6">
                    <div className="flex items-center">
                      <div className="p-2 rounded-lg bg-blue-50 mr-4">
                        {categoryInfo.icon}
                      </div>
                      <div>
                        <h2 className="text-2xl font-bold text-gray-900">{categoryInfo.title}</h2>
                        <p className="text-gray-600">{categoryInfo.description}</p>
                      </div>
                    </div>
                    <button
                      onClick={() => setSelectedCategory(categoryKey)}
                      className="text-blue-600 hover:text-blue-700 text-sm font-medium flex items-center"
                    >
                      View All <ArrowRight className="w-4 h-4 ml-1" />
                    </button>
                  </div>
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {categoryRoadmaps.slice(0, 6).map((roadmap) => (
                      <RoadmapCard
                        key={roadmap.id}
                        roadmap={roadmap}
                        hoveredCard={hoveredCard}
                        setHoveredCard={setHoveredCard}
                      />
                    ))}
                  </div>
                </div>
              );
            })}
          </>
        )}

        {/* Newsletter Section */}
        <div className="bg-gradient-to-r from-blue-600 to-blue-700 rounded-2xl p-8 text-white text-center">
          <h3 className="text-2xl font-bold mb-2">Stay Updated</h3>
          <p className="text-blue-100 mb-6 max-w-2xl mx-auto">
            Get notified about new roadmaps, learning resources, and tech updates delivered to your inbox.
          </p>
          <div className="flex flex-col sm:flex-row gap-3 max-w-md mx-auto">
            <input
              type="email"
              placeholder="Enter your email"
              className="flex-1 px-4 py-3 rounded-lg text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-300"
            />
            <Link href="/Signup">
              <button className="bg-white text-blue-600 font-semibold py-3 px-6 rounded-lg hover:bg-gray-100 transition-colors whitespace-nowrap">
                Subscribe Now
              </button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

// Roadmap Card Component
function RoadmapCard({ roadmap, hoveredCard, setHoveredCard, featured = false }) {
  const { difficulty, duration } = getDifficultyInfo(roadmap.title);
  const isHovered = hoveredCard === roadmap.id;

  const difficultyColors = {
    'Beginner': 'bg-green-100 text-green-800',
    'Intermediate': 'bg-yellow-100 text-yellow-800',
    'Advanced': 'bg-red-100 text-red-800'
  };

  return (
    <Link href={roadmap.link} className="block h-full">
      <div
        className={`bg-white rounded-xl border border-gray-100 hover:border-blue-200 p-6 h-full transition-all duration-300 hover:shadow-lg ${
          isHovered ? 'transform -translate-y-1' : ''
        } ${featured ? 'ring-2 ring-blue-100' : ''}`}
        onMouseEnter={() => setHoveredCard(roadmap.id)}
        onMouseLeave={() => setHoveredCard(null)}
      >
        {featured && (
          <div className="flex items-center justify-between mb-3">
            <span className="bg-yellow-100 text-yellow-800 text-xs font-medium px-2 py-1 rounded-full flex items-center">
              <Star className="w-3 h-3 mr-1" />
              Featured
            </span>
          </div>
        )}

        <div className="flex items-start space-x-4 mb-4">
          <div className="p-3 rounded-lg bg-blue-50 text-blue-600 flex-shrink-0">
            {roadmap.icon}
          </div>
          <div className="flex-1 min-w-0">
            <h3 className="font-bold text-gray-900 text-lg mb-1 truncate">{roadmap.title}</h3>
            <p className="text-gray-600 text-sm line-clamp-2">{roadmap.description}</p>
          </div>
        </div>

        <div className="flex items-center justify-between text-xs text-gray-500 mb-4">
          <div className="flex items-center space-x-3">
            <span className={`px-2 py-1 rounded-full text-xs font-medium ${difficultyColors[difficulty]}`}>
              {difficulty}
            </span>
            <div className="flex items-center">
              <Clock className="w-3 h-3 mr-1" />
              {duration}
            </div>
          </div>
        </div>

        <div className="flex items-center justify-between">
          <span className="text-blue-600 text-sm font-medium">Start Learning</span>
          <ArrowRight className={`w-4 h-4 text-blue-600 transition-transform ${isHovered ? 'translate-x-1' : ''}`} />
        </div>
      </div>
    </Link>
  );
}

// No Results Component
function NoResultsMessage({ onClear }) {
  return (
    <div className="text-center py-16">
      <div className="bg-gray-100 rounded-full w-20 h-20 flex items-center justify-center mx-auto mb-6">
        <Search className="w-10 h-10 text-gray-400" />
      </div>
      <h3 className="text-xl font-semibold text-gray-900 mb-2">No roadmaps found</h3>
      <p className="text-gray-600 mb-6 max-w-md mx-auto">
        We couldn't find any roadmaps matching your search. Try different keywords or browse our categories.
      </p>
      <button
        onClick={onClear}
        className="bg-blue-600 hover:bg-blue-700 text-white font-medium px-6 py-3 rounded-lg transition-colors"
      >
        Show All Roadmaps
      </button>
    </div>
  );
}